import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import ShopA from '../views/shop/ShopA.vue'
//注册全局路由插件
Vue.use(VueRouter)

// const routes = [
//   {
//     path: '/',
//     name: 'home',
//     component: Home
//   },
//    {
//     path: '/about',
//     name: 'About',
//     component: () => import('../views/About.vue')
//    },
