//    },
//    {
//      path:'/shop',
//      name:'Shop',
//      component:() => import('@/views/Shop.vue')
//    }
// ]


const router  =  new VueRouter({
    routes:[
      {
        path: '/',
        name: 'home',
        component: Home,
       
      },
       {
        path: '/about/:idd',
        name: 'About',
        component: () => import('../views/About.vue'),
        meta: { requiresAuth: true }
       },
       {
        path: '/bdd',
        name: 'Bdd',
        component: () => import('../views/Bdd.vue')
        ,
        meta: { requiresAuth: true }
       },

       {
         path:'/shop',
         name:'Shop',
         component:() => import('@/views/Shop.vue'),
         
        meta: { requiresAuth: true },
         //二级路由
         children:[
           {
             path:'/shop/shopA',
             name:'/shop/shopa',
             component:ShopA
            },
            {
              path:'/shop/shopB',
              name:'/shop/shopb',
              component:()=>import('@/views/shop/ShopB.vue')
            }
          ],redirect:'/shop/shopa'//二级路由默认显示
       }
    ],
     linkExactActiveClass:'aadd'//路由切换高亮class自定义类
})

//to下一个路由对象
//from,上一个路由对象
//next，回调函数
router.beforeEach((to,from,next)=>{
      if(to.path.requiresAuth){
            next('/home')
      }else{
        next()
      }
      
})


export default router

